                <el-form-item label="学号" required="true" :label-width="formLabelWidth" prop="id" :rules="[
                  { required: true, message: '留空则自动生成' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input clearable v-model.number="form.id" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="姓名" required="true" :label-width="formLabelWidth" prop="name" :rules="[
                  { required: true, message: '不能为空', trigger: 'blur' },]">
                  <el-input clearable v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="年龄" required="true" :label-width="formLabelWidth" prop="age" :rules="[
                  { required: true, message: '不能为空' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input clearable v-model.number="form.age" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="电话" required="true" :label-width="formLabelWidth" prop="phone" :rules="[
                  { required: true, message: '不能为空' },
                ]">
                  <el-input clearable maxlength="20" show-word-limit v-model="form.phone" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="性别" required="true" :label-width="formLabelWidth">
                  <el-radio-group v-model="form.gender">
                    <el-radio-button label="男"></el-radio-button>
                    <el-radio-button label="女"></el-radio-button>
                  </el-radio-group>
                </el-form-item>

                <el-form-item label="院系" required="true" :label-width="formLabelWidth">
                  <el-radio-group v-model="form.department">
                    <el-radio-button label="计算机"></el-radio-button>
                    <el-radio-button label="土木"></el-radio-button>
                  </el-radio-group>
                </el-form-item>

                <el-form-item label="年级" required="true" :label-width="formLabelWidth">
                  <el-radio-group v-model="form.grade">
                    <el-radio-button label="1"></el-radio-button>
                    <el-radio-button label="2"></el-radio-button>
                    <el-radio-button label="3"></el-radio-button>
                    <el-radio-button label="4"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
              </el-form>

              <div slot="footer" class="dialog-footer">
                <el-button round @click="resetForm('form')">重置</el-button>
                <el-button round @click="dialogFormVisible = false">取消</el-button>
                <el-button type="primary" round @click="form_Submit">确定</el-button>
              </div>
            </el-dialog>&ensp;

            <!-- 按照年级优先自动分配宿舍按钮 -->
            <el-button type="primary" round @click="assign('grade')">按照年级优先分配宿舍</el-button>&ensp;
            <!-- 按照院系优先自动分配宿舍按钮 -->
            <el-button type="primary" round @click="assign('dep')">按照院系优先分配宿舍</el-button>
          </div>

          <br>

          <!-- 条件查询 -->
          <el-form :inline="true" :model="searchForm" class="demo-form-inline" ref="searchForm">
            <el-form-item label="楼栋" prop="unit">
              <el-input v-model="searchForm.unit" placeholder="请输入楼栋"></el-input>
            </el-form-item>

            <el-form-item label="宿舍号" prop="roomid">
              <el-input v-model="searchForm.roomid" placeholder="请输入宿舍号"></el-input>
            </el-form-item>

            <el-form-item label="姓名" prop="name">
              <el-input v-model="searchForm.name" placeholder="请输入姓名(支持模糊查询)"></el-input>
            </el-form-item>

            <el-form-item label="学号" prop="id">
              <el-input v-model="searchForm.id" placeholder="请输入学号"></el-input>
            </el-form-item>

            <el-form-item label="性别" prop="gender">
              <el-select v-model="searchForm.gender" placeholder="请选择性别">
                <el-option label="男" value="男"></el-option>
                <el-option label="女" value="女"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="院系" prop="department">
              <el-select v-model="searchForm.department" placeholder="请选择院系">
                <el-option label="计算机" value="计算机"></el-option>
                <el-option label="土木" value="土木"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="年级" prop="grade">
              <el-select v-model="searchForm.grade" placeholder="请选择年级">
                <el-option label="1" value="1"></el-option>
                <el-option label="2" value="2"></el-option>
                <el-option label="3" value="3"></el-option>
                <el-option label="4" value="4"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="search_Submit()">根据筛选条件查询</el-button>
              <el-button @click="resetForm('searchForm'),search_Submit()">重置</el-button>
            </el-form-item>
          </el-form>


          <!-- 批量删除按钮 -->
          <el-button type="danger" @click="delete_choose()">批量删除已勾选信息</el-button>
          <el-button @click="cancel_choose()">取消</el-button>
          <br><br>

          <!-- 表格数据展示 -->
          <div>
            <el-table :data="stuData" border style="width: 100%" @selection-change="handleSelectionChange" ref="multiple_Table">
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="roommsg" label="楼栋-宿舍号" sortable width="130"></el-table-column>
              <el-table-column prop="id" label="学号" sortable width="80"></el-table-column>
              <el-table-column prop="name" label="姓名" width="100"></el-table-column>
              <el-table-column prop="gender" label="性别" sortable width="80"></el-table-column>
              <el-table-column prop="age" label="年龄" sortable width="80"></el-table-column>
              <el-table-column prop="department" label="院系" sortable width="100"></el-table-column>
              <el-table-column prop="grade" label="年级" sortable width="80"></el-table-column>
              <el-table-column prop="phone" label="电话" width="180"></el-table-column>


              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" @click="
                    //handleEdit(scope.row),
                    EditFormVisible = true,
                    editform.unit = scope.row.unit
                    editform.id = scope.row.id
                    editform.roomid = scope.row.roomid
                    editform.age = scope.row.age
                    editform.name = scope.row.name
                    editform.phone = scope.row.phone
                    editform.gender = scope.row.gender
                    editform.grade = scope.row.grade
                    editform.department = scope.row.department">编辑</el-button>
                  <el-dialog title="填写信息" :visible.sync="EditFormVisible">
                    <el-form :model="editform" ref="editform">
                      <el-form-item label="楼栋" required="true" :label-width="formLabelWidth">
                        <el-input clearable v-model="editform.unit" autocomplete="off"></el-input>
                      </el-form-item>

                      <el-form-item label="宿舍号" required="true" :label-width="formLabelWidth">
                        <el-input clearable v-model="editform.roomid" autocomplete="off"></el-input>
                      </el-form-item>

                      <el-form-item label="学号" required="true" :label-width="formLabelWidth" prop="id" :rules="[
                        { required: true, message: '留空则自动生成' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input clearable v-model.number="editform.id" autocomplete="off" :disabled="true"></el-input>
                      </el-form-item>

                      <el-form-item label="姓名" required="true" :label-width="formLabelWidth" prop="name" :rules="[
                        { required: true, message: '不能为空', trigger: 'blur' },]">
                        <el-input clearable v-model="editform.name" autocomplete="off"></el-input>
                      </el-form-item>

                      <el-form-item label="年龄" required="true" :label-width="formLabelWidth" prop="age" :rules="[
                        { required: true, message: '不能为空' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input clearable v-model.number="editform.age" autocomplete="off"></el-input>
                      </el-form-item>

                      <el-form-item label="电话" required="true" :label-width="formLabelWidth" prop="phone" :rules="[
                        { required: true, message: '不能为空' },
                      ]">
                        <el-input clearable maxlength="20" show-word-limit v-model="editform.phone"
                          autocomplete="off"></el-input>
                      </el-form-item>

                      <el-form-item label="性别" required="true" :label-width="formLabelWidth">
                        <el-radio-group v-model="editform.gender">
                          <el-radio-button label="男"></el-radio-button>
                          <el-radio-button label="女"></el-radio-button>
                        </el-radio-group>
                      </el-form-item>

                      <el-form-item label="院系" required="true" :label-width="formLabelWidth">
                        <el-radio-group v-model="editform.department">
                          <el-radio-button label="计算机"></el-radio-button>
                          <el-radio-button label="土木"></el-radio-button>
                        </el-radio-group>
                      </el-form-item>

                      <el-form-item label="年级" required="true" :label-width="formLabelWidth">
                        <el-radio-group v-model="editform.grade">
                          <el-radio-button label="1"></el-radio-button>
                          <el-radio-button label="2"></el-radio-button>
                          <el-radio-button label="3"></el-radio-button>
                          <el-radio-button label="4"></el-radio-button>
                        </el-radio-group>
                      </el-form-item>
                    </el-form>

                    <div slot="footer" class="dialog-footer">
                      <el-button round @click="resetForm('editform')">重置</el-button>
                      <el-button round @click="EditFormVisible = false">取消</el-button>
                      <el-button type="primary" round @click="edit_Submit()">确定</el-button>
                    </div>
                  </el-dialog>
                  &ensp;
                  <el-button type="danger" size="mini" @click="delete_ids(scope.row.id)">删除</el-button>
                </template>
              </el-table-column>


            </el-table>
          </div>

        </el-main>
      </el-container>
    </el-container>

    <!-- 分页 -->
    <!-- <div class="block" layout="">
      <el-pagination :page-size="10" :pager-count="6" layout="prev, pager, next" :total="100"></el-pagination>
    </div> -->
  </div>
</template>

<style>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
